.header{
  margin-left:0.5rem;
  margin-right:0.5rem;
  --tw-text-opacity:1;
  color:rgba(255, 255, 255, var(--tw-text-opacity))
}

.home__logo{
  position:relative;
  z-index:10;
  padding:24px 0 24px 32px;
  border-bottom:1px solid #FC4E55
}

.home__container{
  position:relative
}

.home__content{
  position:relative;
  z-index:10;
  padding:74px 0 0 32px
}

.home__family-image{
  min-width:250px;
  position:relative;
  margin:0 auto
}

.home__main-container{
  display:flex;
  flex-direction:row
}

.home__family-wrapper{
  flex:1 1
}

.home__background-square{
  position:absolute;
  top:0px;
  width:100%;
  height:100%
}

.home__title{
  font-family:lato;
  font-weight:300;
  font-size:3rem;
  line-height:1;
  --tw-text-opacity:1;
  color:rgba(255, 255, 255, var(--tw-text-opacity))
}

.home__title-assurance{
  font-size:1.5rem;
  line-height:2rem
}

.home__title-assurance-red{
  --tw-text-opacity:1;
  color:rgba(239, 51, 64, var(--tw-text-opacity))
}

.home__subtitle{
  font-family:lato;
  font-weight:400;
  font-size:3rem;
  line-height:1;
  --tw-text-opacity:1;
  color:rgba(255, 255, 255, var(--tw-text-opacity))
}

.home__form{
  width:300px;
  max-width:100%;
  margin:90px auto 0 auto
}

.footer__text{
  --tw-text-opacity:1;
  color:rgba(255, 255, 255, var(--tw-text-opacity))
}

@media (min-width: 1024px){
